import React,{Component} from 'react';

import Section, {Sec, About, Formacao, Historico, Portifolio} from '../Section/Section';
//import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { HashRouter, Route, Link } from "react-router-dom";
import './style.css';

{/*Esse componente é o que se encaixa
como nossa Barra de Navegação */}

class Navr extends Component{
    render(){
        return(
          <HashRouter basename="/">
          
            <nav className="navbar  navbar-transparente fixed-top" >
      
      <div className="container">
        <div className="navbar-header">

       

          
        <a href="" className="navbar-brand">
          
          <span className="img-logo">Rodrigo.</span>
        </a>
       
       
          
         </div>
         
         <div  id="barra-navegacao">
           
           <ul className="nav ">
             <li><Link to="/">Home</Link></li>
             <li><Link to="/About">Sobre</Link></li>
             <li><Link to="/Formacao">Formação</Link></li>
             <li><Link to="/Historico">Histórico</Link></li>
             <li><Link to="/Portifolio">Portifólio</Link></li>
           </ul>
           
           
         
   
           </div>
          
        
        
      </div>
    </nav>
    
   
   </HashRouter>
        );
    }
}

export default Navr;