import React, { Component } from 'react';

import Navr from './components/navbar/NavBar';

import Cap from './components/Capa/Capa';

//import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import { HashRouter, Route, Link } from "react-router-dom";
import Section, {Sec, About, Formacao, Historico, Portifolio} from './components/Section/Section';


import Foo from './components/Footer/Footer';


{/*Importando os components para a tela em branco app*/}

{/*componente que renderiza no nó principal
e gerencia todo os componentes do site
*/}


class App extends Component {
  render() {
  
    return (
      <HashRouter basename="/">
      
      <div className="App">
       <Navr />
       <Cap titulo="Rodrigo Miguel da Costa"/>
      
       <Route path="/" exact component={Sec} />
       <Route path="/About" exact component={About} />
       <Route path="/Formacao" exact component={Formacao} />
       <Route path="/Historico" exact component={Historico} />
       <Route path="/Portifolio" exact component={Portifolio} />
      
       <Foo />
      </div>
      
      </HashRouter>
    );
  }
}

export default App;
