import React,{Component} from 'react';

import './style.css'

import Gallery from 'react-photo-gallery';

{/*Esse componente é o que se encaixa
como todo o conteudo do site (não me decidi se vai ser unico)*/}

class Sec extends Component{
render(){
    return(
    <section className="cor">

    <div className="container" >

    <div className="row">
    
    <div className="col-md-6 ">
    <h2>OLÁ, TUDO BEM COM VOCÊ ?</h2>
    <p >
    Meu nome é Rodrigo e pra falar de mim é preciso falar de desenvolvimento.
carrego comigo algumas lições que a programação me deixou ao longo dos anos: você deve sempre tentar de novo, você é responsável por aquilo que faz e que estamos sempre aprendendo.
Carrego comigo uma vontade intrínseca em vencer e fazer a diferença. O meio me ensinou isso, e hoje eu gostaria de mostrar para vocês através do meu site o que venho aprendendo.

    </p>
    
    </div>

    <div className="col-md-6 ">
    <h2>POR QUE ESCOLHI SER DESENVOLVEDOR ?</h2>
    <p>
    Desde que eu comecei meu primeiro curso de Tecnologia da informação me encantei 
    pela área de desenvolvimento e toda a "estranheza" que era mexer com uma área tão moderna como o T.I .
    ainda assim demorou muito para eu ter 
   contato com o que de fato eu escolheria como profissão, o mercado aquecido e a comunidade acolhedora me proporcionaram boas experiências ao longo dos ano
   seja no ambiente acadêmico ou profissional. Familiarizar-me com todos os códigos e linguagens que havia na área não foram fáceis, mas quem quer e gosta do que faz consegue! Então atualmente já sou formado e exerço a profissão de desenvolvedor web.

     
    </p>
    
    
    </div>
    

    </div>

  
    </div>
      
    </section>

    
    );
}


}




class About extends React.Component {
    render() {
       return (
         <section className="cor">

         <div className="container" >
     
         <div className="row">
         
         <div className="col-md-6 ">
         <h2>QUEM SOU EU ?</h2>
         <p >
         <img  className=" ima " src={require('./rodrigo.jpg')}></img><br></br>

         Meu nome é, Rodrigo, nasci em 1998 e sou Desenvolvedor Web. Minhas experiencias na área de T.I começaram aos 16 anos quando iniciei um curso generalista sobre o assunto, no mesmo abordava diversos temas sobre a profissão mas até então não fazia idéia do que estava fazendo e onde estava me metendo, porém mesmo assim aprendi bastante conceitos de como programar partindo dali, somente na faculdade comecei a aperfeiçoar minhas skills web e arranhar a superfície de diversa linguagens. 
   Ja formado continuo me esforçando e estudando sempre para melhorar e desenvolver minhas habilidades como programador desde então. 

     
         </p>
         
         </div>

         <div className="col-md-6 ">
         <h2>"Na maioria dos casos, forças e fraquezas são dois lados da mesma moeda. Uma força em uma situação é uma fraqueza em outra, mas frequentemente as pessoas não conseguem trocar as marchas. É uma coisa muito sutil falar sobre forças e fraquezas porque elas sempre são a mesma coisa. Steve Jobs"</h2>
        
         
         </div>
     
      
         
     
         </div>
     
       
         </div>
           
         </section>
       )
    }
 }


